.container {
  width: 375px;
  min-height: 18vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.row {
  width: 375px;
  min-height: 16.6vh;
  background: white;
  display: flex;
  justify-content: center;
}

.searchbar-input {
  width: 375px;
  height: 84px;
  font-size: 14px;
  box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
}
input[type="search"] {
  border: none;
  background: #f0f3f4;
  border-radius: 8px;
  width: 246px;
  height: 36px;
  margin: 25px 0;
  font-family: FontAwesome;
}
input[type="submit"] {
  width: 64px;
  height: 36px;
  background: #452cdc;
  color: white;
  border-radius: 8px;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  margin-left: 15px;
  cursor: pointer;
}
.col {
  width: 100%;
  display: flex;
  flex-direction: column;
  min-height: 90vh;
}

img {
  width: 64px;
  border-radius: 12px;
  margin-right: 24px;
}

.photo-login,
.bio,
.repo-buttons {
  width: 327px;
  min-height: 40px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 10px 0px 10px 25px;
}
.repo-buttons {
  flex-direction: column;
  background: #FFFFFF;
}
.bio {
  color: #828282;
}
.subtitle {
  color: #000000;
}
.repo-button {
  width: 327px;
  height: 48px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 2px 7px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  margin-bottom: 0.6em;
  padding: 0;
}
.repo-button a {
  margin-left: 16px;
  color: #2f80ed;
  text-decoration: none;
}
